.search-bar-ctn {
  background: #f1f3f4;
  display: flex;
  align-items: center;
  height: 40px;
  border-radius: 8px;
}

.search-bar-input {
  background: transparent;
  border: none;
  outline: none;
  transition: width 0.5s;
  width: 180px;
}

.search-bar-input:focus {
  width: 250px;
}

.search-hint-ctn {
  position: absolute;
  padding: 10px;
  max-height: 250px;
  border: 1px solid #5555;
  border-top: none;
  list-style-type: none;
  overflow-y: auto;
  top: 24px;
  background-color: white;
}

.hint-item {
  cursor: pointer;
  line-height: 2;
  border-top: 1px solid #dedede;
}

.hint-item:hover {
  background-color: #e6e7e8;
}
