.like-ctn {
  margin-right: 6px;
  height: 19px;
  cursor: pointer;
}

.like-ctn:hover .in-active {
  display: inline;
}
.like-ctn:hover .active {
  display: none;
}

.in-active {
  width: 19px;
  height: 19px;

  opacity: 0.7;
  display: none;
}

.active {
  width: 19px;
  height: 19px;

  opacity: 0.7;
}
